@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500;700&family=Poppins:wght@200;300;400;500;600;700&display=swap');
:root{
    --bg-black-900:#f2f2fc;
    --bg-black-100:#fdf9ff;
    --bg-black-50:#e8dfec;
    --text-black-900:#302e4d;
    --text-black-700:#504e70;
    --skin-color:#ec1839;

}
body{
    line-height: 1.5;
    font-family:'Poppins' sans-serif;
    background:#000000e3

}
*
{
    margin: 0;
    padding: 0;
    outline: none;
    text-decoration: none;
    box-sizing: border-box;
}
::after,::before{
    box-sizing: border-box;
}
ul{
    list-style: none;
}
.pad-15{
    padding-left: 15px;
    padding-right: 15px;
}
.container{
    width: 1024px;
    position:absolute;
    top: 3rem;
    margin-left:300px;
    margin-right:4rem;
    color: var(--bg-black-100);
}
.section{
    min-height:100%; 
    padding: 0px;
    right: 0;
    z-index: 0;
    opacity: 1;
}
.row{
    margin-bottom: 5rem;
}


/* Aside css start */

.pad-15{
    padding-left: 15px;
    padding-right: 15px;
}


.aside{
    width: 230px;
    background: var(--bg-black-900);
    position: fixed;
    display: flex;
    left: 0;
    top: 0;
    padding: 10px;
    height: 100%;
    justify-content: center;
    align-items: center;
}
.nav{
    position: fixed;
   flex-direction: column;
}
.aside .logo{
    position: absolute;
    top: 30px;
    font-size: 30px;
    text-transform: capitalize;
}
.aside .logo a{
    color: var(--text-black-900);
    font-weight: 700;
    padding: 12px 13px;
    font-size: 17px;
    letter-spacing: 5px;
    font-style:italic;
    position: relative;
}
.aside .logo a span{
    font-family: 'Clicker Script',cursive;

}
.aside .logo a::before{
    content: '';
    position:absolute;
    width: 20px;
    min-height: 20px;
    border-top: 4px solid var(--skin-color);
    border-left: 4px solid var(--skin-color);
    top: 0;
    left: 0;

}
.aside .logo a::after{
    content: '';
    position:absolute;
    width: 20px;
    min-height: 20px;
    border-bottom: 4px solid var(--skin-color);
    border-right: 4px solid var(--skin-color);
    bottom: 1px;
    right:-0.1rem;
}
.aside .nav-toogler {
    height: 40px;
    min-width: 45px;
    border: 1px solid var(--bg-black-50);
    cursor: pointer;
    position: fixed;
    left: 300px;
    top: 20px;
    border-radius:5px;
    background: var(--bg-black-100);
    display: none;
    align-items: center;
    justify-content: center;
}
.aside .nav-toogler span{
    height: 2px;
    width: 18px;
    background: var(--skin-color);
    display:inline-block;
    position:relative;

}
.aside .nav-toogler span::before{
    content: '';
    min-height: 2px;
    width: 18px;
    background: var(--skin-color);
    position:absolute;
    top: -6px;
    left: 0;

}
.aside .nav-toogler span::after{
    content: '';
    min-height: 2px;
    min-width: 18px;
    background: var(--skin-color);
    position:absolute;
    top: 6px;
    left: 0;
}
.aside .nav li{
    margin-bottom: 20px;
    display: block;
}
.aside .nav li a {
    font-size: 25px;
    font-weight: 400;
    display: block ;
    border-bottom: 1px solid var(--bg-black-50);
    color: var(--text-black-900);
    padding: 5px 15px;

}
.aside .nav li a.active{
    color: var(--skin-color);
}
.aside .nav li a i{
    margin-right: 10px;
}





.contact-title::after{
    content: '';
    height: 4px;
    width: 80px;
    background-color: var(--skin-color);
    position: absolute;
    left: 0;
}
.contact-title::before{
    content: '';
    height: 4px;
    width: 60px;
    background-color: var(--skin-color);
    position: absolute;
    top:3.5rem;
    left: 0;
}
.contact-item-container{
    text-align:center;
}
.contact-sub-title{
    margin-top: 4rem;
    color: var(--skin-color);
    font-size: 30px;
    font-weight: 700;
}
.contact-sub-title-2{
    color: var(--bg-black-100);
    text-align: center;
    font-size: 15px;
    font-weight: 500;

}
.contact-info-container{
    margin-top: 4rem;
    display: flex;
}
.contact-info-item{
    flex: 0 0 28%;
}
.icon .fa{
    color: var(--skin-color);
    font-size: 25px;
}
.contact-info-item h4{
    font-size: 20px;
    font-weight: 700;
    margin: 10px 0 5px;
}
.contact-info-item p{
    font-size: 16px;
    line-height: 25px;
    font-weight: 400;
}
.contact-inputs{
    display: flex;
    gap: 3rem;
}
.form-control{
    flex: 0 0 55%;
    margin:3rem 0 0 0;
    width: 115%;
    height: 50px;
    border-radius:25px;
    background: var(--bg-black-100);
    border: 1px solid var(--bg-black-100);
    padding: 10px 25px;
    font-size: 16px;
    color: var(--text-black-700);
    transition: all 0.3s ease;
}
.btn{
    font-size: 16px;
    padding: 8px 20px;
    font-weight: 500;
    color: white;
    border-radius: 40px;
    display: inline-block;
    white-space: nowrap;
    border: none;
    background: var(--skin-color);
    transition: all 0.3s ease;
}
.btn:hover{
    transform: scale(1.05);
}
.form-control:focus{
    box-shadow: 0 0 20px rgba(233, 232, 237, 0.59);
}
textarea.form-control{
    height: 140px;
}
#Message{
    margin-bottom: 3rem;
}




/* Responsive Start */


@media (max-width:930px)
{
.aside{
    display: none;
}
.container{
     margin-left:30px;
     top: 4rem;
}



}